<template>
  <v-app-bar app clipped-left>
    <v-img :src="logoSrc" alt="logo" max-height="40" max-width="40" />
    <v-toolbar-title class="ml-3"
      >BubbleJet WebSocket Control Panel</v-toolbar-title
    >

    <v-spacer />

    <div class="d-flex">
      <div>
        {{ $t("status") }}{{ $t("separator")
        }}<ConnectionStatus :connected="connected" />
      </div>
    </div>

    <v-btn @click="onUpdate" class="ml-3 align-self-center">{{
      $t("disconnect")
    }}</v-btn>
  </v-app-bar>
</template>

<script>
import { mapState } from "vuex";
import ConnectionStatus from "./ConnectionStatus";

export default {
  name: "AppBar",

  components: { ConnectionStatus },

  computed: {
    ...mapState({
      logoSrc: (state) =>
        state.config.darkTheme
          ? require("../assets/logo-dark.svg")
          : require("../assets/logo-light.svg"),
      serverUrl: (state) => state.connection.serverUrl,
      connected: (state) => state.connection.connected,
    }),
  },

  methods: {
    onUpdate() {
      this.$emit("update");
    },
  },
};
</script>
