<template>
  <v-dialog
    :value="isOpen"
    transition="dialog-bottom-transition"
    max-width="300"
    persistent
  >
    <v-card>
      <v-card-title>{{ $t("connection.title") }}</v-card-title>
      <v-card-text>
        <form @submit.prevent="onSubmit">
          <v-select
            v-model="serverUrl"
            :label="$t('host')"
            :value="serverUrl"
            :items="serverUrlOptions"
          />
          <v-btn
            :loading="isConnecting"
            :disabled="isConnecting || !isValid"
            type="submit"
            class="primary"
            >{{ $t("connection.connect") }}</v-btn
          >
          <div v-if="error" class="mt-3 red--text">
            {{ errorMessage }}
          </div>
        </form>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: "ConnectionModal",

  props: {
    isOpen: Boolean,
    isConnecting: Boolean,
    error: String,
    initialServerUrl: String,
  },

  data() {
    return {
      serverUrl: this.initialServerUrl,
      serverUrlOptions: [
        {
          text: "Development",
          value: "http://localhost:5000/admin",
        },
        {
          text: "Beta",
          value: "https://beta.bubblejet.app/admin",
        },
        {
          text: "Production",
          value: "https://bubblejet.app/admin",
        },
      ],
    };
  },

  computed: {
    isValid() {
      return this.serverUrl;
    },
    errorMessage() {
      return this.$t("connection.error") + this.$t("separator") + this.error;
    },
  },

  methods: {
    onSubmit() {
      this.$emit("submit", {
        serverUrl: this.serverUrl,
      });
    },
  },
};
</script>

<style scoped></style>
